// IMPORT

var widgetEl1 = document.getElementById('widget-1');
var widget2ButtonEls = document.querySelectorAll('#widget-2 button');
var events = ['arrowUpKey', 'arrowDownKey', 'arrowLeftKey', 'arrowRightKey', 'escapeKey', 'spacebarKey', 'enterKey', 'homeKey', 'endKey', 'pageDownKey', 'pageUpKey'];

// on widget1 container

KeyEmitter.add(widgetEl1);
events.forEach(function (eventName) {
  widgetEl1.addEventListener("".concat(eventName, "Down"), function (e) {
    console.log(this, e);
  });
  widgetEl1.addEventListener("".concat(eventName, "Up"), function (e) {
    console.log(this, e);
  });
});

// on widget2 buttons

[...widget2ButtonEls].forEach(function (el) {
  KeyEmitter.add(el);
  events.forEach(function (eventName) {
    el.addEventListener("".concat(eventName, "Down"), function (e) {
      console.log(this, e);
    });
    el.addEventListener("".concat(eventName, "Up"), function (e) {
      console.log(this, e);
    });
  });
});